import DataService from "@/service/DataService";
let buttons = [];
let leftsearchSchema = [
    {
        fieldName: "Location",
        fieldType: "ddl-multilevel",
        fieldIcon: "fa-file",
        paramName:"LocationId",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
        listOptions: []
    }, 
    {
        fieldName: 'Product Name/Code',
        fieldType: "text",
        fieldIcon: "fa-user",
        paramName:"search",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
    },
    {
        fieldName: 'Status',
        fieldType: "radio",
        fieldIcon: "fa fa-file-text-o",
        paramName:"statusIds",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
        listOptions: []
    },
    {
        fieldName: 'Tag',
        fieldType: "ddl-check",
        fieldIcon: "fa-server",
        paramName:"EPCTags",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
        listOptions: []
    }, 
]
 
function bindleftCommonSearchdropdown(instance,modulename, filtername) {
    
    var vm = instance;
    var LocationData=[];
    var TagData=[]
   
    if (filtername == "Location") {
        
        var params  = `reqForm=`
        DataService.GetLocationByCompanyId(params)
         .then(function (response) {
            var json =vm.MakeNormalArray(response.data);
            json=json.data;
            $.each(json, $.proxy(function (index, item) {
                var  groupId = item.PARENT_LOCATION_ID;
                     var chkGroup = $.grep(LocationData, function (element, index) {
                         return element.id == groupId;
                     });
                     if (chkGroup.length == 0) {
                     var returnedData = $.grep(json, function (element, index) {
                             return element.PARENT_LOCATION_ID == groupId;
                         });
                         if (returnedData.length > 0 && returnedData[0].LOCATION_NAME) {                                         
                             var objGroup = {
                                 id: groupId,
                                 text: returnedData[0].LOCATION_NAME,
                                 icon:'fa fa-map-marker',
                                 children: returnedData.map((item) => {                                              
                             return {id:item.COMPANY_LOCATION_ID,text:item.LOCATION_NAME,icon:'fa fa-map-marker'}
                             })
                             };
                             LocationData.push(objGroup);
                         }
                       }
                })) 
                console.log(LocationData);               
                vm.leftSearchFields[0].listOptions =LocationData;
        });
     }
    if (filtername == "Tag") {
       var params = `modulename=${modulename}`; 
        DataService.GetTagsByModuleId(params)
        .then(function (response) {
            
            if (filtername == "Tag") {
                let obj1={}
                JSON.parse(response.data).data.forEach(item => {
                    obj1={                    
                        name:item.TAG_NAME,
                        value:`${item.TAG_ID}`
                    }
                    TagData.push(obj1);
                });
                vm.leftSearchFields[3].listOptions =TagData;
        }
        });
    }
    if (filtername == "Status") {
       var params = `langCode=en&statusType=AssetManagement&hasglobal=true`; 
        DataService.GetStatusList(params)
        .then(function (response) {
            
            if (filtername == "Status") {
                let obj2={}
                JSON.parse(response.data).forEach(item => {
                    obj2={                    
                        name:item.STATUS,
                        value:`${item.STATUS_ID}`
                    }
                    TagData.push(obj2);
                });
                vm.leftSearchFields[2].listOptions =TagData;
        }
        });
    }    
}
export default {buttons,leftsearchSchema,bindleftCommonSearchdropdown};